.country {
   padding: 60px 0 30px;
   &_container {
      @media (min-width: 767px) {
         display: flex;
         align-items: center;
         justify-content: space-between;
         gap: 60px;
      }
   }
   &_flag {
      
      box-shadow: var(--shadow);
      border-radius: var(--rad);
      overflow: hidden;
      max-width: 100%;
      @media (min-width: 767px) {
         flex: 1 1 40%;
         margin-bottom: 30px;
      }
      @media (max-width: 767px) {
         margin-bottom: 30px;
      }
   }
   &_info {
      display: flex;
      flex-direction: column;
      gap: 40px;
      @media (max-width: 500px) {
         gap: 20px;
      }
      @media (min-width: 767px) {
         flex: 1 1 40%;
      }
   }
   &_title {
      font-weight: var(--fw-bold);
   }
   &_body {
      display: flex;
      flex-direction: column;
      gap: 30px;
   }
   &_lists {
      gap: 10px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
   }
   &_list {
      display: flex;
      flex-direction: column;
      gap: 10px;
      span {
         font-weight: var(--fw-bold);
      }
   }
   &_borders {
      display: flex;
      flex-direction: column;
      gap: 10px;
   }
   &_wrapper {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
      align-items: center;
   }
   &_border {
      text-align: center;
      white-space: nowrap;
      flex: 0 1 10%;
      padding: 10px;
      background-color: var(--color-elements);
      box-shadow: var(--shadow);
      border-radius: var(--rad);
   }
}
